"use client";
import { useEffect, useState } from "react";
import dynamic from "next/dynamic";
import dayjs from "dayjs";

// âœ… Dynamically import Plotly to avoid SSR issues
const Plot = dynamic(() => import("react-plotly.js"), { ssr: false });

interface SP500StockData {
    Date: string;
    Open: number;
    High: number;
    Low: number;
    Close: number;
    Volume: number;
    Ticker: string;
    Year: number;
    Month: number;
    Day: number;
    moving_avg_20: number;
    volatility_20: number;
    moving_avg_50: number;
    volatility_50: number;
    Change: number;
}

interface PerformanceChartProps {
    data: SP500StockData[];
    ticker: string;
}

export default function PerformanceChart({ data, ticker }: PerformanceChartProps) {
    const [loading, setLoading] = useState(true);

    const [startDate, setStartDate] = useState(dayjs().subtract(1, "year").format("YYYY-MM-DD"));
    const [endDate, setEndDate] = useState(dayjs().format("YYYY-MM-DD"));
    const [showMovingAvg20, setShowMovingAvg20] = useState(true);
    const [showMovingAve50, setShowMovingAvg50] = useState(true);

    useEffect(() => {
        if (Array.isArray(data) && data.length > 0) {
            setLoading(false);
        } else {
            setLoading(true);
        }
    }, [data]);

    if (loading) {
        return (
            <div className="flex justify-center items-center h-64">
                <div className="animate-spin rounded-full h-12 w-12 border-t-4 border-b-4 border-gray-400"></div>
            </div>
        );
    }

    if (!Array.isArray(data)) {
        console.error("PerformanceChart received invalid data:", data);
        return <div className="pt-4 shadow-lg bg-white">No valid data available for {ticker}</div>;
    }

    const filteredData = data
        .filter(stock => stock.Ticker === ticker && dayjs(stock.Date).isBetween(startDate, endDate, "day", "[]"))
        .map(stock => ({
            ...stock,
            Date: dayjs(stock.Date).format("YYYY-MM-DD"),
        }));

    if (filteredData.length === 0) {
        return <div className="p-4 rounded-lg shadow-lg bg-white">No data available for {ticker}</div>;
    }

    return (
        <div className="p-4 rounded-lg">
            {/* Filters Section */}
            <div className="flex items-center gap-4 mb-4">
                {/* Date Pickers */}
                <label className="text-white"> Start Date: </label>
                <input
                    type="date"
                    value={startDate}
                    onChange={(e) => setStartDate(e.target.value)}
                    className="bg-gray-800 text-white p-2 rounded"
                />
                <label className="text-white">End Date: </label>
                <input
                    type="date"
                    value={endDate}
                    onChange={(e) => setEndDate(e.target.value)}
                    className="bg-gray-800 text-white p-2 rounded"
                />

                {/* toggle Moving Averages */}
                <label className="text-white flex items-center">
                    <input
                        type="checkbox"
                        checked={showMovingAvg20}
                        onChange={() => setShowMovingAvg20(!showMovingAvg20)}
                        className="mr-2"
                    />
                </label>
                <label className="text-white flex items-center">
                    <input
                        type="checkbox"
                        checked={showMovingAvg50}
                        onChange={() => setShowMovingAvg50(!showMovingAvg50)}
                        className="mr-2"
                    />
                </label>
            </div>

            <Plot
                data={[
                    {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.Close),
                        type: "scatter",
                        mode: "lines",
                        name: "Close Price",
                        line: { color: "#00FF00", width: 2 },
                    },
                    showMovingAvg20 && {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.moving_avg_20),
                        type: "scatter",
                        mode: "lines",
                        name: "20-Day Moving Avg",
                        line: { color: "#FF00FF", width: 2, dash: "dash" },
                    },
                    showMovingAvg50 && {
                        x: filteredData.map(stock => stock.Date),
                        y: filteredData.map(stock => stock.moving_avg_50),
                        type: "scatter",
                        mode: "lines",
                        name: "50-Day Moving Avg",
                        line: { color: "#00FFFF", width: 2, dash: "dot" },
                    },
                ].filter(Boolean)} // Remove undefined values
                layout={{
                    autosize: true,
                    plot_bgcolor: "black",  // Dark background
                    paper_bgcolor: "black", // Dark container background
                    font: { color: "#ffffff" }, // White text
                    xaxis: {
                        title: "Date",
                        tickformat: "%Y-%m-%d",
                        showgrid: true,
                        gridcolor: "#333", // Subtle grid lines
                    },
                    yaxis: {
                        title: "Stock Price",
                        showgrid: true,
                        gridcolor: "#333",
                    },
                    margin: { t: 10, l: 50, r: 10, b: 50 },
                    legend: {
                        bgcolor: "#1e1e1e", // Dark legend box
                        font: { color: "#ffffff" },
                    },
                }}
                useResizeHandler
                style={{ width: "100%", height: "400px" }}
            />
        </div>
    );
}

